import React, { Component } from 'react';
import WowItemList from 'components/wowitem/WowItemList';
import {connect} from 'react-redux';
import {bindActionCreators} from 'redux';
import * as wowitemActions from 'store/modules/wowitem';

class WowItemListContainer extends Component {

  getWowItemList = async() => {
    const {WowitemActions} = this.props;
    try {
      await WowitemActions.getWowItemList();
    } catch(e) {
      console.log(e);
    }
  }

  componentDidMount() {
    const {wowitems} = this.props;
    if(!wowitems.length > 0) this.getWowItemList();
  }

  selStat = (stat) => {
    const {WowitemActions} = this.props;
    WowitemActions.setStatOption(stat)
  }

  selSubClass = (subClass) => {
    const {WowitemActions} = this.props;
    WowitemActions.setItemSubClass(subClass)
  }

  render() {
    const {wowitems, isCri, isHas, isMas, isVer, itemSubClass} = this.props;
    const {selStat, selSubClass} = this;

    return (
      
      <WowItemList
      wowitems={wowitems}
      selStat={selStat}
      selSubClass={selSubClass}
      isCri={isCri}
      isHas={isHas}
      isMas={isMas}
      isVer={isVer}
      itemSubClass={itemSubClass}
    //   isStockDownView={isStockDownView}
    //   todayMax={todayMax}
      />
    );
  }
}

export default connect(
  (state) => ({
    wowitems: state.wowitem.toJS().wowitems,
    isCri: state.wowitem.toJS().isCri,
    isHas: state.wowitem.toJS().isHas,
    isMas: state.wowitem.toJS().isMas,
    isVer: state.wowitem.toJS().isVer,
    itemSubClass: state.wowitem.toJS().itemSubClass
  }),
  (dispatch) => ({
    WowitemActions: bindActionCreators(wowitemActions, dispatch)
  })
)(WowItemListContainer);